<template>
    
    <Button icon="bi bi-pen text-primary" text rounded severity="info" @click="dialogVisible = true"/>

    <Dialog v-model:visible="dialogVisible" modal :header="`Güncelle: ${props.team.name}`" class="p-dialog-fullscreen">

        <form @submit.prevent="updateTeam">

            <div class="row align-items-center">
                <label class="col-6 form-label">ANA GÖRSEL</label>
                <div class="col-6">
                    <div class="d-flex flex-row align-items-end">
                        <div class="symbol symbol-100px">
                            <img :src="form.media_objects.mainImage.preview_url" v-if="form.media_objects.mainImage"/>
                            <div class="symbol-label" v-else>
                                <i class="bi bi-person fs-2x"></i>
                            </div>
                        </div>
                        <div class="ms-3 d-flex flex-row align-items-center">
                            <PopupMediaLibrary :on-select="setMainImage" :button-text="'Seç'" :mime-type="'image/'" :key="Math.floor(Math.random() * 100000)"/>
                            <button type="button" class="btn btn-sm btn-light-danger ms-2" v-if="form.media_objects.mainImage" @click="form.media_objects.mainImage = null">Kaldır</button>
                        </div>
                    </div>
                </div>
            </div>

            <Divider />

            <div class="row align-items-center">
                <label class="col-lg-6 form-label">Şube Adı</label>
                <div class="col-lg-6">
                    <input class="form-control form-control-sm" v-model="form.name" type="text"/>
                </div>
            </div>

            <Divider />

            <div class="row align-items-center">
                <label class="col-lg-6 form-label">Adres</label>
                <div class="col-lg-6">
                    <textarea class="form-control form-control-sm" v-model="form.address"></textarea>
                </div>
            </div>

            <Divider />

            <div class="row align-items-center">
                <label class="col-lg-6 form-label">Google Koor.</label>
                <div class="col-lg-6">
                    <input class="form-control form-control-sm" v-model="form.coordinates" type="text" />
                </div>
            </div>

            <Divider />

            <div class="row align-items-center">
                <label class="col-lg-6 form-label">Biletix URL</label>
                <div class="col-lg-6">
                    <input class="form-control form-control-sm" v-model="form.biletix" type="text" />
                </div>
            </div>
            <div class="separator my-5"></div>
            <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>
            <FormErrors :form="form"/>

        </form>

    </Dialog>
</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import Button from 'primevue/button';
import Divider from "primevue/divider";
import PopupMediaLibrary from "../MediaLibrary/PopupMediaLibrary";

const props = defineProps({
    team:Object
});

const dialogVisible = ref(false);

const form = useForm({
    name:props.team.name,
    coordinates:props.team.coordinates,
    address:props.team.address,
    biletix:props.team.biletix,
    media_objects:{
        mainImage:props.team.media_objects.mainImage,
        headerImage:props.team.media_objects.headerImage,
        mainVideo:props.team.media_objects.mainVideo,
        headerImage:props.team.media_objects.headerImage,
        galleryImages:props.team.media_objects.galleryImages,
    },
});

const setMainImage = (media) => {
    form.media_objects.mainImage = media;
}

const updateTeam = () => {
    form.put(route('teams.update',props.team),{
        preserveScroll: true,
        onSuccess: () => {
            form.reset();
            dialogVisible.value = false;
        },
    });
}


</script>